import React, { useRef, useEffect, useState, useCallback } from "react";
import { useDispatch } from "react-redux";
import clsx from "clsx";
import * as Yup from "yup";
import PropTypes from "prop-types";
import { Formik } from "formik";
import {
  Box,
  Button,
  Checkbox,
  TextField,
  Typography,
  makeStyles,
  Grid,
  FormControlLabel,
  Paper,
  CardContent,
  Card,
  CardHeader,
  Divider,
  FormHelperText,
} from "@material-ui/core";
import DraftEditor from "src/components/DraftEditor";
import {
  EditorState,
  convertToRaw,
  convertFromHTML,
  ContentState,
  convertFromRaw,
} from "draft-js";
import draftToHtml from "draftjs-to-html";
import { editBLogRequest } from "src/actions/blogActions";
import { getAreaRequest } from "src/actions/areaActions";
import ImageDropzone from "src/components/ImageDropzone";
import convertPathSeo from "src/utils/convertPathSeo";

const useStyles = makeStyles(() => ({
  root: {},
}));
var listStatus = [
  { id: 0, name: "ang thc hin" },
  { id: 1, name: " thc hin" },
];
function ProductEditForm({
  className,
  onSubmitSuccess,
  onSubmitFailed,
  projectDetail,
  ...rest
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const sortRef = useRef(null);
  const [editorState, setEditorState] = useState(() =>
    // EditorState.createWithContent(ContentState.createFromText("hakshdk"))
    EditorState.createWithContent(
      ContentState.createFromBlockArray(convertFromHTML("<p>a</p>"))
    )
  );
  const handleEditorChange = (state) => {
    console.log("state", state);
    // setEditorState(state);
    setBody(draftToHtml(convertToRaw(state.getCurrentContent())));
  };
  const [showPassword, setShowPassword] = useState(false);
  const [isOpen, setIsOpen] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [statusProject, setStatusProject] = useState(listStatus[0].id);
  const [body, setBody] = useState("");
  const [dataDetail, setDataDetail] = useState("");
  const [thumbnail, setThumbnail] = useState("");
  const customContentStateConverter = (contentState) => {
    // changes block type of images to 'atomic'
    const newBlockMap = contentState.getBlockMap().map((block) => {
      const entityKey = block.getEntityAt(0);
      if (entityKey !== null) {
        const entityBlock = contentState.getEntity(entityKey);
        console.log(";entityBlock", entityBlock.getData());
        const entityType = entityBlock.getType();
        switch (entityType) {
          case "IMAGE": {
            const newBlock = block.merge({
              type: "atomic",
              text: "img",
            });
            return newBlock;
          }
          default:
            return block;
        }
      }
      return block;
    });
    const newContentState = contentState.set("blockMap", newBlockMap);
    return newContentState;
  };
  useEffect(() => {
    if (projectDetail !== undefined && projectDetail.title !== undefined) {
      console.log("projectDetail", projectDetail);
      setDataDetail(projectDetail);
      setThumbnail(projectDetail.thumbnail);
      setStatusProject(projectDetail.status);
      setIsOpen(projectDetail.highlight === 1 ? true : false);
      const blocksFromHTML = convertFromHTML(projectDetail.body);
      var editorStateCV = EditorState.createWithContent(
        customContentStateConverter(
          ContentState.createFromBlockArray(
            blocksFromHTML.contentBlocks,
            blocksFromHTML.entityMap
          )
        )
      );
      setEditorState(editorStateCV);
      setBody(projectDetail.body);
    }
  }, [projectDetail]);

  return (
    <div>
      {dataDetail !== "" && (
        <Formik
          initialValues={{
            body: body,
            name: dataDetail.title,
            description: dataDetail.description,
            source: dataDetail.source,
          }}
          validationSchema={Yup.object().shape({
            name: Yup.string()
              .max(255)
              .required("Thng tin bt buc"),
          })}
          onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
            setIsLoading(true);
            try {
              var newBody = body
                .replaceAll("height: auto", "height: 260px")
                .replaceAll("height: undefined", "height: 260px")
                .replaceAll(
                  "width: undefined",
                  "width: 576px;object-fit: cover;"
                )
                .replaceAll("width: auto", "width: 576px;object-fit: cover;");
              var objet = {
                id: dataDetail._id,
                pathSEO: convertPathSeo(values.name),
                title: values.name,
                body: newBody,
                thumbnail: thumbnail,
                description: values.description,
                source: values.source,
                highlight: isOpen ? 1 : 0,
              };
              await dispatch(
                editBLogRequest(
                  objet,
                  (res) => {
                    setStatus({ success: true });
                    setSubmitting(false);
                    setIsLoading(false);
                    onSubmitSuccess();
                  },
                  (error) => {
                    onSubmitFailed();
                  }
                )
              );
            } catch (error) {
              setStatus({ success: false });
              setErrors({ submit: error.message });
              setSubmitting(false);
            }
          }}
        >
          {({
            errors,
            handleBlur,
            handleChange,
            handleSubmit,
            isSubmitting,
            touched,
            values,
          }) => (
            <form
              className={clsx(classes.root, className)}
              onSubmit={handleSubmit}
              {...rest}
            >
              <Grid container spacing={3}>
                <Grid item xs={12} lg={12}>
                  <Card>
                    <CardContent>
                      <Grid
                        className={clsx(classes.root, className)}
                        container
                        justify="space-between"
                        spacing={3}
                      >
                        <Grid item xs={12} lg={6}>
                          <TextField
                            error={Boolean(touched.name && errors.name)}
                            fullWidth
                            helperText={touched.name && errors.name}
                            label="Tn bi vit"
                            name="name"
                            onBlur={handleBlur}
                            onChange={handleChange}
                            value={values.name}
                            variant="outlined"
                          />
                          <TextField
                            error={Boolean(
                              touched.description && errors.description
                            )}
                            fullWidth
                            helperText={
                              touched.description && errors.description
                            }
                            label="Gii thiu ngn gn"
                            margin="normal"
                            onBlur={handleBlur}
                            onChange={handleChange}
                            type="text"
                            value={values.description}
                            variant="outlined"
                            style={{ marginTop: 30 }}
                            multiline
                            rows={5}
                            name="description"
                          />
                          <TextField
                            style={{ marginTop: 20 }}
                            error={Boolean(touched.source && errors.source)}
                            fullWidth
                            helperText={touched.source && errors.source}
                            label="Ngun"
                            name="source"
                            onBlur={handleBlur}
                            onChange={handleChange}
                            value={values.source}
                            variant="outlined"
                          />
                          <div style={{ display: "flex" }}>
                            <FormControlLabel
                              control={
                                <Checkbox
                                  checked={isOpen}
                                  onChange={() => {
                                    setIsOpen(!isOpen);
                                  }}
                                  name="inStock"
                                />
                              }
                              label="D n ni bt"
                            />
                          </div>
                        </Grid>
                        <Grid item xs={12} lg={6}>
                          <Box>
                            <Card>
                              <CardHeader title="Ti nh i din" />
                              <Divider />
                              {thumbnail !== "" ? (
                                <CardContent>
                                  <div
                                    style={{
                                      display: "flex",
                                      flexDirection: "column",
                                      alignItems: "center",
                                    }}
                                  >
                                    <img
                                      style={{
                                        width: 300,
                                        height: 200,
                                        objectFit: "cover",
                                      }}
                                      src={thumbnail}
                                    />
                                    <Box mt={1}>
                                      <Button
                                        color="secondary"
                                        size="large"
                                        variant="contained"
                                        onClick={() => {
                                          setThumbnail("");
                                        }}
                                      >
                                        Xo
                                      </Button>
                                    </Box>
                                  </div>
                                </CardContent>
                              ) : (
                                <CardContent>
                                  <ImageDropzone setThumbnail={setThumbnail} />
                                </CardContent>
                              )}
                            </Card>
                          </Box>
                        </Grid>
                      </Grid>

                      <Box mt={3} mb={1}>
                        <Typography variant="subtitle2" color="textSecondary">
                          Ni dung
                        </Typography>
                      </Box>
                      <Paper
                        style={{ minHeight: 500, padding: 20 }}
                        variant="outlined"
                      >
                        {/* <QuillEditor
                      className={classes.editor}
                      value={values.description}
                      onChange={(value) => setFieldValue("description", value)}
                    /> */}
                        <DraftEditor
                          style={{ minHeight: 500 }}
                          editorState={editorState}
                          onEditorStateChange={handleEditorChange}
                        />
                      </Paper>
                      {touched.description && errors.description && (
                        <Box mt={2}>
                          <FormHelperText error>
                            {errors.description}
                          </FormHelperText>
                        </Box>
                      )}
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
              <Box mt={2}>
                <Button
                  color="secondary"
                  disabled={isSubmitting}
                  fullWidth
                  size="large"
                  type="submit"
                  variant="contained"
                >
                  Cp nht
                </Button>
              </Box>
            </form>
          )}
        </Formik>
      )}
      {isLoading && <div class="loading"></div>}
    </div>
  );
}

ProductEditForm.propTypes = {
  className: PropTypes.string,
  onSubmitSuccess: PropTypes.func,
};

ProductEditForm.default = {
  onSubmitSuccess: () => {},
};

export default ProductEditForm;
